import React, { Component }  from 'react'
import { Link } from 'react-router-dom'
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react'
import { Button, Card, CardImg,CardBody, CardGroup, Col, Container, Form, Input, InputGroup, InputGroupAddon, InputGroupText, Row, CardHeader } from 'reactstrap';
import CIcon from '@coreui/icons-react'
import logo from '../../../assets/o-terrain-logo-png.png'
import $ from 'jquery'
import axios from 'axios'
import  { Redirect } from 'react-router-dom'
import conf from '../../../conf.js'
import './success.css'
class Success extends Component {

  constructor(props) {
    super(props)
    this.state = {
       ok:''
    }
  
  }

  componentDidMount() {}

  render() {

    const titre=this.props.titre
    const message=this.props.message
    const boutton=this.props.boutton
    const handleValidate = this.props.handleValidate

    return (
      <>
        {/* <div className="c-app c-default-layout flex-row align-items-center"> */}
          {/* <div id='card' class="animated fadeIn"> */}
            <div id='upper-side'>
            
                <h3 id='status'>
                {titre}
                </h3>
            </div>
            <div id='lower-side'>
              <p id='message'>
                {message}
              </p>
              <a onClick={handleValidate} id="contBtn">{boutton}</a>
            </div>
          {/* </div> */}
        {/* </div> */}
      </>
    )
  }
}
export default Success
